/*heights and widths*/
.h-screen{
    height: 100vh;
}
.w-400{
    width: 400px;
}

/* inputs */
input , select{
    height: 40px;
    border: 2px solid gray;
    width: 100%;
    padding-left: 20px;
}
.ant-form-item{
    margin: 0 !important;
}

/*card*/
.card{
    border: 2px solid gray;
    box-shadow: 0 0 1px gray;
    border-radius: 1 !important;
}

/* text-sizes */
.text-2xl{
    font-size: 30px !important;
}

.text-xl{
    font-size: 25px !important;
}

.text-lg{
    font-size: 20px !important;
}

.text-md{
    font-size: 15px !important;
}

.underline{
    text-decoration: underline;
    cursor: pointer;
}

.text-secondary{
    color: var(--secondary) !important;
}

.text-primary{
    color: var(--primary) !important;
}

/* buttons */
.primary-btn{
    background-color: var(--primary);
    color: white;
    padding: 10px 20px;
    cursor: pointer;
}


.secondary-btn{
    background-color: var(--secondary);
    color: white;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
}

.outlined{
    border: 2px solid var(--primary) !important;
    color: var(--primary) !important;
    background-color: white !important;
}
.disabled-btn{
    background-color: gray !important;
    color: white;
    border: none !important;
    padding: 10px 20px;
    border-radius: 4px !important;
    cursor: not-allowed;
}

button{
    border-radius: 4px !important;
}


/* anchor tag */
a{
    text-decoration: none !important;
    color: var(--primary) !important;
}

i{
    cursor: pointer;
    font-size: 25px;
}
h1, p{
    margin-bottom: 0;
    padding-bottom: 0;
}
/* Loader */
.spinner-parent{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0, 0.507);
    z-index: 10000;
}

.spinner-border{
    color: white;
    height: 80px;
    width: 80px;

}

table{
    border: 1px solid rgb(177, 177, 177) !important;
}